function formatList(arr, keyword) {
		let newArr1 = [];
		let tempArr = [];
		let reg = /\b(\w)|\s(\w)/g;
		let k = 0;
		let firstWord = arr[0][keyword].charAt(0).toUpperCase(); //
		arr.map((v) => {
			v[keyword] = v[keyword].replace(reg, m => m.toUpperCase()); //
			if (firstWord == v[keyword].charAt(0)) {
				tempArr.push(v);
				newArr1[k] = {
					Title: firstWord,
					List: tempArr
				}
			} else {
				//
				firstWord = v[keyword].charAt(0); //
				tempArr = []; //
				tempArr.push(v); //
				newArr1[++k] = { //
					Title: firstWord,
					List: tempArr
				}
			}
		});
		return newArr1;
	}
	
	export {
		formatList
	}
